import React from "react"

import { BsTwitter } from "react-icons/bs"
import { BsInstagram } from "react-icons/bs"
import { BsLinkedin } from "react-icons/bs"

function Footer() {
  return (
    <footer style={{ backgroundColor: "#ededed" }}>
      <div className="container py-5">
        <div className="row align-items-center justify-content-between">
          <div className="col-auto order-lg-1">
            <div className="d-flex">
              <a
                target="_blank"
                href="http://www.twitter.com/dbankGlobal"
                className="text-dark me-3 me-lg-0 ms-lg-3"
              >
                <BsTwitter />
              </a>
              <a
                target="_blank"
                href="http://www.instagram.com/dbankglobal"
                className="text-dark me-3 me-lg-0 ms-lg-3"
              >
                <BsInstagram />
              </a>
              <a
                target="_blank"
                href="http://www.linkedin.com/company/dbank"
                className="text-dark me-3 me-lg-0 ms-lg-3"
              >
                <BsLinkedin />
              </a>
            </div>
          </div>
          <div className="col-auto order-lg-0">
            <p className="mt-3 mt-lg-0 m-0" style={{ fontSize: 12 }}>
              Copyrights 2022 | Din Global. All Rights Reserved.
            </p>
          </div>
        </div>
      </div>
      <div className="bg-primary p-2"></div>
    </footer>
  )
}

export default Footer
